.wysiwyg__h1 {
  line-height: .9;
}

.wysiwyg__h2 {
  font-size: 3.25rem;
  line-height: 1;
}

.wysiwyg__h3 {
  font-size: 2.2rem;
}

.wysiwyg__h4 {
  font-size: 1.75rem;
  line-height: 1.45;
}

.wysiwyg__h5 {
  line-height: 1.5;
}

.wysiwyg__h6 {
  line-height: 1.5;
}

.wysiwyg__p {
  line-height: 1.6;
}

.wysiwyg__td {
  vertical-align: top !important;
  /* border-bottom: 1px solid #ccc; */
  padding: inherit; 
}
.wysiwyg__tr {
  border-bottom: 1px solid #ccc;
  padding: 10px 0px;
}

